import React, { Fragment } from "react";
import { connect } from "react-redux";
//import DesktopHeader from "components/header/desktopHeader";
import LoginComponent from "../components/loginComponent";
import { saveLogin, fetchCityBe , fetchCityZh } from "../actions/dispatchAction/fetchData";

class LoginContainer extends React.Component {
    saveLogin = () => {
        this.props.saveLogin(true);
    }

    fetchCityBe = () => {
        this.props.fetchCityBe();
    };
    fetchCityZh = () => {
        this.props.fetchCityZh();
      };
    componentDidMount() {
        //this.props.fetchData();
    }
    render() {
        return (                       
        <LoginComponent saveLogin = {this.saveLogin} fetchCityBe={this.fetchCityBe} fetchCityZh={this.fetchCityZh}/>                    
        )

    }
    }
LoginContainer.propTypes = {};
LoginContainer.defaultProps = {};
const mapStateToProps = state => {
    return state
};
const mapDispatchToProps = dispatch => ({
    fetchCityBe : () => {
        dispatch(fetchCityBe())
    },

    fetchCityZh : () => {
        dispatch(fetchCityZh())
    },

    saveLogin : (flag) => {
        dispatch(saveLogin(flag))
    },
});
export default connect(mapStateToProps, mapDispatchToProps)(LoginContainer);