import React from 'react';
import styles from './pieListComponent.module.scss';
import applyWrappers from '../../wrappers/ComponentWrapper';
import Card from '@material-ui/core/Card';
import { ResponsivePie } from '@nivo/pie'

const PieListComponent = (props) => {
    const colors= ["#00B388","#425563","#80746E","#5F7A76","#DCDCDC","#708090"];
    const finalData =[];
    const renderAllcard = (groupByData)=>{
        let obj;
        for(let i=0; i<groupByData.length ; i++){
            obj = renderCard(groupByData[i], i);
            finalData.push(obj);
        }
        return finalData;
    }

    const renderCard = (eachData ,index)=>{
        const info = eachData.userByType;
        let data = [];
        for(let i=0; i< info.length; i++){
            let obj = {  "id": info[i].typeName, "label": info[i].typeName, value: info[i].typeLen , color: colors[i]};
            data.push(obj);
        }
        return(
            // <Card styleName="card">
                <div  key={index} styleName="pie">
                <div styleName="title">{eachData.functionTitle}</div>
                <ResponsivePie
                    data={data}
                    margin={{ top: 0, right: 100, bottom: 60, left: 20 }}
                    innerRadius={0.4}
                    cornerRadius={1}
                    colors={colors}
                    borderWidth={1}
                    borderColor={{ from: 'color', modifiers: [ [ 'darker', 0.2 ] ] }}
                    enableRadialLabels={false}
                    radialLabelsSkipAngle={10}
                    radialLabelsTextXOffset={6}
                    radialLabelsTextColor="#333333"
                    radialLabelsLinkOffset={0}
                    radialLabelsLinkDiagonalLength={16}
                    radialLabelsLinkHorizontalLength={24}
                    radialLabelsLinkStrokeWidth={1}
                    radialLabelsLinkColor={{ from: 'color' }}
                    slicesLabelsSkipAngle={10}
                    slicesLabelsTextColor="#333333"
                    animate={true}
                    motionStiffness={90}
                    motionDamping={15}
                    isInteractive={true}
                    fill={[
                        {
                            match: {
                                id: 'ruby'
                            },
                            id: 'dots'
                        },
                        {
                            match: {
                                id: 'c'
                            },
                            id: 'dots'
                        },
                        {
                            match: {
                                id: 'go'
                            },
                            id: 'dots'
                        },
                        {
                            match: {
                                id: 'python'
                            },
                            id: 'dots'
                        },
                        {
                            match: {
                                id: 'scala'
                            },
                            id: 'lines'
                        },
                        {
                            match: {
                                id: 'lisp'
                            },
                            id: 'lines'
                        },
                        {
                            match: {
                                id: 'elixir'
                            },
                            id: 'lines'
                        },
                        {
                            match: {
                                id: 'javascript'
                            },
                            id: 'lines'
                        }
                    ]}
                    legends={[
                        {
                            dataFrom: 'keys',
                            anchor: 'right',
                            direction: 'column',
                            justify: false,
                            translateX: 105,
                            translateY: 0,
                            itemsSpacing: 2,
                            itemWidth: 100,
                            itemHeight: 20,
                            itemDirection: 'left-to-right',
                            overflowWrap: 'break-word',
                            itemOpacity: 0.85,
                            symbolSize: 20,
                            effects: [
                                {
                                    on: 'hover',
                                    style: {
                                        itemOpacity: 1
                                    }
                                }
                            ]
                        }
                    ]}
                />
            {/* </Card> */}
            </div>
        )
    }
    return (
        <div styleName="container">
            {renderAllcard(props.groupByData)}
        </div>	
    );
}
export default applyWrappers(PieListComponent, styles);