import React from "react";
import { connect } from "react-redux";
import styles from "./sidebarComponent.module.scss";
import applyWrappers from "../../wrappers/ComponentWrapper";
import HPE_log_left_wht from "./HPE_log_left_wht.png";
import updateRoute from "../../../history";
import {
  saveLogin,
  fetchFeedbackSession,
  fetchCustomerData,
  fetchCustomerFunction,
  fetchNoFeedback,
  fetchExpansionPanel,
  fetchSalesPanel,
  fetchSession,
  fetchRegisteredUsers
} from "../../actions/dispatchAction/fetchData";
class SidebarComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isVisibleFeedback: false,
      isVisibleCustomer: false
    };
    this.toggleFeedback = this.toggleFeedback.bind(this);
    this.toggleCustomer = this.toggleCustomer.bind(this);

    this.fetchRegisteredUsers();
  }
  logout = () => {
    this.props.saveLogin(false);
  };
  fetchRegisteredUsers = () => {
    this.props.fetchRegisteredUsers();
  };
  fetchSession = () => {
    this.props.fetchSession();
  };

  fetchFeedbackSession = () => {
    this.props.fetchFeedbackSession();
  };

  fetchNoFeedback = () => {
    this.props.fetchNoFeedback();
  };

  fetchExpansionPanel = () => {
    this.props.fetchExpansionPanel();
  };

  fetchSalesPanel = () => {
    this.props.fetchSalesPanel();
  };

  fetchCustomerData = () => {
    this.props.fetchCustomerData();
  };
  fetchCustomerFunction = () => {
    this.props.fetchCustomerFunction();
  };

  toggleFeedback() {
    this.setState({ isVisibleFeedback: !this.state.isVisibleFeedback });
  }

  toggleCustomer() {
    this.setState({ isVisibleCustomer: !this.state.isVisibleCustomer });
  }

  onClick = (e, ele, { isCustomerData = false, feedback = false }) => {
    e.stopPropagation();
    switch (ele) {
      case "feedback":
        if (feedback == "session") {
          this.fetchFeedbackSession();
        } else if (feedback == "noFeedback") {
          this.fetchNoFeedback();
        } else if (feedback == "expansion") {
          this.fetchExpansionPanel();
        } else if (feedback == "sales") {
          this.fetchSalesPanel();
        }
        break;
      case "customer":
        if (isCustomerData) {
          this.fetchCustomerData();
        } else {
          this.fetchCustomerFunction();
        }
        break;
      case "session":
        this.fetchSession();
        break;
      case "login":
        this.logout();
        break;
      default:
        console.log("false");
        break;
    }
    updateRoute({ route: "/" + ele });
  };

  render() {
    return (
      <div className={styles.wrapper}>
        <div className={styles.top_navbar}>
          <div className={styles.top_menu}>
            <div className={styles.logo} className={styles.logo_top}>
              <img src={HPE_log_left_wht} alt="HPE" height="50" />
            </div>
          </div>
        </div>
        <div className={styles.sidebar}>
          <ul className={styles.ul1}>
            <li
              onClick={e =>
                this.onClick(e, "dashboard", { isCustomerData: false })
              }
            >
              <a
                className={
                  this.props.active == "dashboard" ? styles.active : ""
                }
              >
                <span className={styles.title}>Home</span>
              </a>
            </li>
            <li
              onClick={e =>
                this.onClick(e, "registration", { isCustomerData: false })
              }
            >
              <a
                className={
                  this.props.active == "registration" ? styles.active : ""
                }
              >
                <span className={styles.title}>Registrations</span>
              </a>
            </li>
            <li>
              <a
                className={this.props.active == "customer" ? styles.active : ""}
                onClick={() => this.toggleCustomer()}
              >
                <span className={styles.title}>Customers</span>
              </a>
            </li>
            {this.state.isVisibleCustomer && (
              <div className="container">
                <div
                  className={styles.subItem}
                  onClick={e =>
                    this.onClick(e, "customer", { isCustomerData: true })
                  }
                >
                  Customer Data
                </div>
                <div
                  className={styles.subItem}
                  onClick={e =>
                    this.onClick(e, "customer", { isCustomerData: false })
                  }
                >
                  Customer Function
                </div>
              </div>
            )}
            <li
              onClick={e =>
                this.onClick(e, "session", { isCustomerData: false })
              }
            >
              <a
                className={this.props.active == "session" ? styles.active : ""}
              >
                <span className={styles.title}>Sessions</span>
              </a>
            </li>
            <li
              onClick={e =>
                this.onClick(e, "entrance", { isCustomerData: false })
              }
            >
              <a
                className={this.props.active == "entrance" ? styles.active : ""}
              >
                <span className={styles.title}>Entrances</span>
              </a>
            </li>
            <li>
              <a
                className={this.props.active == "feedback" ? styles.active : ""}
                onClick={() => this.toggleFeedback()}
              >
                <span className={styles.title} key="feedback">
                  Feedback
                </span>
              </a>
            </li>
            {this.state.isVisibleFeedback && (
              <div className="container">
                <div
                  className={styles.subItem}
                  onClick={e =>
                    this.onClick(e, "feedback", { feedback: "session" })
                  }
                >
                  Feedback Session
                </div>
                <div
                  className={styles.subItem}
                  onClick={e =>
                    this.onClick(e, "feedback", { feedback: "expansion" })
                  }
                >
                  Feedback Event
                </div>
                <div
                  className={styles.subItem}
                  onClick={e =>
                    this.onClick(e, "feedback", { feedback: "sales" })
                  }
                >
                  Feedback For Sales
                </div>
                <div
                  className={styles.subItem}
                  onClick={e =>
                    this.onClick(e, "feedback", { feedback: "noFeedback" })
                  }
                >
                  Feedback Not Given
                </div>
              </div>
            )}
            <li
              onClick={e => this.onClick(e, "login", { isCustomerData: false })}
            >
              <a>
                <span className={styles.title}>Logout</span>
              </a>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}
SidebarComponent.propTypes = {};
SidebarComponent.defaultProps = {};
const mapStateToProps = state => {
  return state;
};
const mapDispatchToProps = dispatch => ({
  fetchRegisteredUsers: () => {
    dispatch(fetchRegisteredUsers());
  },

  fetchFeedbackSession: () => {
    dispatch(fetchFeedbackSession());
  },

  fetchCustomerData: () => {
    dispatch(fetchCustomerData());
  },

  fetchCustomerFunction: () => {
    dispatch(fetchCustomerFunction());
  },

  fetchSession: () => {
    dispatch(fetchSession());
  },

  fetchNoFeedback: () => {
    dispatch(fetchNoFeedback());
  },

  fetchExpansionPanel: () => {
    dispatch(fetchExpansionPanel());
  },

  fetchSalesPanel: () => {
    dispatch(fetchSalesPanel());
  },
  saveLogin: flag => {
    dispatch(saveLogin(flag));
  }
});
export default connect(mapStateToProps, mapDispatchToProps)(SidebarComponent);
