import React from "react";
import styles from "./entranceComponent.module.scss";
import applyWrappers from "../../wrappers/ComponentWrapper";
import SidebarComponent from "../sidebarComponent";
import TableDataComponent from "../tableDataComponent";

const EntranceComponent = props => {
  return (
    <div>
      <SidebarComponent active="entrance" />
      <div styleName="subcontainer">
        <div>
          <h2 styleName="title">HPE Innovation Circle</h2>
          <h3 styleName="sub-title">
          {props.action.city == "be" ?  "Bern | 2. April 2020" : "Dbendorf | 7. April 2020"}
      </h3>
          <div styleName="desc">Entrances/No-Show</div>
          <table styleName="partner">
            <tr>
              <td>Partner:</td>
              <td>
                <select
                  name="ctl00$ContentPlaceHolder1$ddlOrganizers"
                  onchange="javascript:setTimeout(&#39;__doPostBack(\&#39;ctl00$ContentPlaceHolder1$ddlOrganizers\&#39;,\&#39;\&#39;)&#39;, 0)"
                  id="ContentPlaceHolder1_ddlOrganizers"
                  styleName="all"
                >
                  <option selected="selected" value="">
                    Alle
                  </option>
                  <option value="29">Hewlett Packard Enterprise</option>
                  <option value="114">HPE Partner</option>
                  <option value="116">Medien</option>
                  <option value="117">Meetup</option>
                </select>
              </td>
            </tr>

            <tr>
              <td>Day:</td>
              <td>
                <select
                  name="ctl00$ContentPlaceHolder1$ddlDays"
                  onchange="javascript:setTimeout(&#39;__doPostBack(\&#39;ctl00$ContentPlaceHolder1$ddlDays\&#39;,\&#39;\&#39;)&#39;, 0)"
                  id="ContentPlaceHolder1_ddlDays"
                  styleName="selected"
                >
                  <option selected="selected" value="">
                    Alle
                  </option>
                  <option value="2">Dienstag, 7.4.2020</option>
                </select>
              </td>
            </tr>

            <tr>
              <td>Entrances or No-Shows:</td>
              <td>
                <select
                  name="ctl00$ContentPlaceHolder1$ddlEntrancesOrNoShows"
                  onchange="javascript:setTimeout(&#39;__doPostBack(\&#39;ctl00$ContentPlaceHolder1$ddlEntrancesOrNoShows\&#39;,\&#39;\&#39;)&#39;, 0)"
                  id="ContentPlaceHolder1_ddlEntrancesOrNoShows"
                >
                  <option selected="selected" value="True">
                    Entrances
                  </option>
                  <option value="False">No-Shows</option>
                </select>
              </td>
            </tr>

            <tr>
              <td>Number of search results:</td>
              <td>0</td>
            </tr>

            <tr>
              <td></td>
              <td>
                <input
                  type="submit"
                  name="ctl00$ContentPlaceHolder1$ButtonExport"
                  value="Show"
                  styleName="showbtn"
                  id="ContentPlaceHolder1_ButtonExport"
                />
              </td>
            </tr>
          </table>
          <div>
            <table
              class="grid"
              cellspacing="0"
              id="ContentPlaceHolder1_gvUsers"
              styleName="grid"
            >
              <tr>
                <td colspan="6">No matching users found.</td>
              </tr>
            </table>
            <div styleName="tableData">            
              <TableDataComponent/>
            </div> 
          </div>
          <br />
        </div>
      </div>
    </div>
  );
};
export default applyWrappers(EntranceComponent, styles);
