import React from "react";
import styles from "./feedbackCustomerListComponent.module.scss";
import applyWrappers from "../../wrappers/ComponentWrapper";
import TableDataComponent from "../tableDataComponent";

const FeedbackCustomerListComponent = props => { 
  return (
    <div>
        <div>
          <h2 styleName="title">HPE Innovation Circle</h2>
          <h3 styleName="sub-title">{ props.action.city == "be" ? "Bern | 2. April 2020" : "Dbendorf | 7. April 2020" }</h3>
          <table styleName="partner">
            <tr>
              <td>Partner:</td>
              <td>
                <select
                  name="ctl00$ContentPlaceHolder1$ddlOrganizers"
                  onchange="javascript:setTimeout(&#39;__doPostBack(\&#39;ctl00$ContentPlaceHolder1$ddlOrganizers\&#39;,\&#39;\&#39;)&#39;, 0)"
                  id="ContentPlaceHolder1_ddlOrganizers"
                  styleName="all"
                >
                  <option selected="selected" value="">
                    Alle
                  </option>
                  <option value="29">Customer</option>
                  <option value="114">HPE Partner</option>
                  <option value="116">Sponsor</option>
                  <option value="117">HPE Employee</option>
                </select>
              </td>
            </tr>
            <tr>
              <td>In Case of HPE Employees:</td>
              <td>
                <select
                  name="ctl00$ContentPlaceHolder1$ddlOrganizers"
                  onchange="javascript:setTimeout(&#39;__doPostBack(\&#39;ctl00$ContentPlaceHolder1$ddlOrganizers\&#39;,\&#39;\&#39;)&#39;, 0)"
                  id="ContentPlaceHolder1_ddlOrganizers"
                  styleName="all">
              
                  <option selected="selected" value="">
                    Alle
                  </option>
                  <option value="11">Referent</option>       
                  <option value="12">Sales</option>
                  <option value="13">Pre-Sales</option>
                  <option value="14">Expo-Staff</option>
                  <option value="15">Management</option>
                  <option value="16">Logistik-Staff</option>
                  <option value="20">Management Management</option>
                  

                </select>
              </td>
            </tr>
            <tr>
              <td>Rating:</td>
              <td>
                <select
                  name="ctl00$ContentPlaceHolder1$ddlDays"
                  onchange="javascript:setTimeout(&#39;__doPostBack(\&#39;ctl00$ContentPlaceHolder1$ddlDays\&#39;,\&#39;\&#39;)&#39;, 0)"
                  id="ContentPlaceHolder1_ddlDays"
                  styleName="selected"
                >
                  <option selected="selected" value="">
                    Alle
                  </option>
                  <option value="2">5 Star</option>       
                  <option value="3">4 Star</option>
                  <option value="4">3 Star</option>
                  <option value="5">2 Star</option>
                  <option value="6">1 Star</option>
                  

                </select>
              </td>
            </tr>

          
            <tr>
              <td>Number of search results:</td>
              <td>0</td>
            </tr>

            <tr>
              <td></td>
              <td>
                <input
                  type="submit"
                  name="ctl00$ContentPlaceHolder1$ButtonExport"
                  value="Show"
                  styleName="showbtn"
                  id="ContentPlaceHolder1_ButtonExport"
                />
              </td>
            </tr>
          </table>
          <div>
            <table
              class="grid"
              cellspacing="0"
              id="ContentPlaceHolder1_gvUsers"
              styleName="grid"
            >
              <tr>
                <td colspan="6">No matching users found.</td>
              </tr>
            </table>
            <div styleName="tableData">            
              <TableDataComponent/>
            </div> 
          </div>
          <br />
        </div>
      </div>
  );
};
export default applyWrappers(FeedbackCustomerListComponent, styles);
