import React from 'react';
import styles from './pieByFunctionsComponent.module.scss';
import applyWrappers from '../../wrappers/ComponentWrapper';
import SidebarComponent from "../sidebarComponent";
import { ResponsivePie } from '@nivo/pie';
import _ from 'lodash';
const convert = require('xml-js');

const PieByFunctionsComponent = (props) => {
    let customerType = [];
    let functionTitle = []
    let groupByData = [];
    if(props.action.registeredUsers){
        let data0 = props.action.registeredUsers;
        data0 = JSON.parse(convert.xml2json(data0.documentElement.innerHTML,{compact: true,  spaces: 4}));
        const registeredUsers = data0['soap:Body']['LoadAllRegisteredUsersOfActiveEditionResponse']['LoadAllRegisteredUsersOfActiveEditionResult']['diffgr:diffgram']['DocumentElement']['tblOutput'];
        registeredUsers.filter(eachUser => {
            if((customerType.indexOf(eachUser['UserType_Title']['_text']) == -1)){
            customerType.push(eachUser['UserType_Title']['_text']);
            }
            if((functionTitle.indexOf(eachUser['Function_Title']['_text']) == -1)){
                functionTitle.push(eachUser['Function_Title']['_text']);
            }
        });

        for(let i=0 ; i < customerType.length ; i++){
            let type = customerType[i];
            var users = _.filter(registeredUsers, function(user) {
              if (user['UserType_Title']['_text'] == type) {
                return user;
              }
            });
            let userByFunction = [];
            for(let j=0 ; j < functionTitle.length ; j++){     
                let funName = functionTitle[j];
                var usersByFun = _.filter(users, function(user) {
                  if (user['Function_Title']['_text'] == funName) {
                    return user;
                  }
                });
                userByFunction.push({funName , funLen: usersByFun.length })
            }
            groupByData.push({type, len: users.length,userByFunction});
        }
    }

   
 

    const colors= ["#00B388","#425563","#80746E","#5F7A76","#DCDCDC","#708090" ,"#000000" ,"#2d170e","#cc9f88"];
    const finalData =[];
    const renderAllPie = (groupByData)=>{
        let obj;
        for(let i=0; i<groupByData.length ; i++){
            obj = renderPie(groupByData[i],i);
            finalData.push(obj);
        }
        return finalData;
    }

    const renderPie = (pieData, index)=>{
        const info = pieData.userByFunction;
        let data = [];
        for(let i=0; i< info.length; i++){
            let obj = {  "id": info[i].funName, "label": info[i].funName, value: info[i].funLen , color: colors[i]};
            data.push(obj);
        }
        return(
            <div key={index} styleName="pie">
                <div styleName="title1">{pieData.type}</div>
                <ResponsivePie
                    data={data}
                    margin={{ top: 0, right: 140, bottom: 60, left: 20 }}
                    innerRadius={0.4}
                    cornerRadius={1}
                    colors={colors}
                    borderWidth={1}
                    borderColor={{ from: 'color', modifiers: [ [ 'darker', 0.2 ] ] }}
                    enableRadialLabels={false}
                    radialLabelsSkipAngle={10}
                    radialLabelsTextXOffset={6}
                    radialLabelsTextColor="#333333"
                    radialLabelsLinkOffset={0}
                    radialLabelsLinkDiagonalLength={16}
                    radialLabelsLinkHorizontalLength={24}
                    radialLabelsLinkStrokeWidth={1}
                    radialLabelsLinkColor={{ from: 'color' }}
                    slicesLabelsSkipAngle={10}
                    slicesLabelsTextColor="#333333"
                    animate={true}
                    motionStiffness={90}
                    motionDamping={15}
                    isInteractive={true}
                    fill={[
                        {
                            match: {
                                id: 'ruby'
                            },
                            id: 'dots'
                        },
                        {
                            match: {
                                id: 'c'
                            },
                            id: 'dots'
                        },
                        {
                            match: {
                                id: 'go'
                            },
                            id: 'dots'
                        },
                        {
                            match: {
                                id: 'python'
                            },
                            id: 'dots'
                        },
                        {
                            match: {
                                id: 'scala'
                            },
                            id: 'lines'
                        },
                        {
                            match: {
                                id: 'lisp'
                            },
                            id: 'lines'
                        },
                        {
                            match: {
                                id: 'elixir'
                            },
                            id: 'lines'
                        },
                        {
                            match: {
                                id: 'javascript'
                            },
                            id: 'lines'
                        }
                    ]}
                    legends={[
                        {
                            dataFrom: 'keys',
                            anchor: 'right',
                            direction: 'column',
                            justify: false,
                            translateX: 105,
                            translateY: 0,
                            itemsSpacing: 2,
                            itemWidth: 100,
                            itemHeight: 20,
                            itemDirection: 'left-to-right',
                            overflowWrap: 'break-word',
                            itemOpacity: 0.85,
                            symbolSize: 20,
                            effects: [
                                {
                                    on: 'hover',
                                    style: {
                                        itemOpacity: 1
                                    }
                                }
                            ]
                        }
                    ]}
                />
            </div>
        )
    }
    return (
        <div>
            <SidebarComponent active="customer"/>
            <div styleName="container">
                <div styleName="main-heading">HPE Innovation Circle</div>
                <h3 styleName="sub-title"> 
                {props.action.city == "be" ?  "Bern | 2. April 2020" : "Dbendorf | 7. April 2020"}
                </h3>
                <h2 styleName="title"> 
                    Customer registrations by function
                </h2>
                <div styleName="sub-container">
                    {renderAllPie(groupByData)}
                </div>	
            </div>
        </div>
    );
}
export default applyWrappers(PieByFunctionsComponent, styles);