import React from "react";
import styles from "./expansionComponent.module.scss";
import applyWrappers from "../../wrappers/ComponentWrapper";
import { withStyles } from '@material-ui/core/styles';
import MuiExpansionPanel from '@material-ui/core/ExpansionPanel';
import MuiExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import MuiExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import UserRatingComponent from "../userRatingComponent";

const ExpansionComponent = props => {
    const ExpansionPanel = withStyles({
        root: {
          border: '1px solid rgba(0, 0, 0, .125)',
          boxShadow: 'none',
          '&:not(:last-child)': {
            borderBottom: 0,
          },
          '&:before': {
            display: 'none',
          },
          '&$expanded': {
            margin: 'auto',
            paddingBottom: '19px',
          },
        },
        expanded: {},
      })(MuiExpansionPanel);
      
      const ExpansionPanelSummary = withStyles({
        root: {
          backgroundColor: 'rgba(0, 0, 0, .03)',
          borderBottom: '1px solid rgba(0, 0, 0, .125)',
          marginBottom: -1,
          minHeight: 56,
          '&$expanded': {
            minHeight: 56,
          },
        },
        content: {
          '&$expanded': {
            margin: '12px 0'
          },
        },
        expanded: {
          
        },
      })(MuiExpansionPanelSummary);
      
      const ExpansionPanelDetails = withStyles(theme => ({
        root: {
          padding: theme.spacing(2),
        },
      }))(MuiExpansionPanelDetails);
      
        const [expanded, setExpanded] = React.useState('panel1');
      
        const handleChange = panel => (event, newExpanded) => {
          setExpanded(newExpanded ? panel : false);
        };
      
        return (
        <div>
            <h2 styleName="title">
                    
                HPE Innovation Circle
            </h2>
            <h3 styleName="sub-title">    
            { props.action.city == "be" ? "Bern | 2. April 2020" : "Dbendorf | 7. April 2020" }
            </h3>
            <div styleName="title1">Feedback for the Event</div>
            <div styleName="expansion"> 
              <ExpansionPanel square expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                <ExpansionPanelSummary aria-controls="panel1d-content" id="panel1d-header">
                  <Typography>How do you judge the value of this event?</Typography>
                </ExpansionPanelSummary>
                <div styleName="rating"> 
                <UserRatingComponent fetchFeedbackCustomer={props.fetchFeedbackCustomer} width={"100%"}/>
                </div>
              </ExpansionPanel>
              <ExpansionPanel square expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                <ExpansionPanelSummary aria-controls="panel2d-content" id="panel2d-header">
                  <Typography>How well can you implement what you hear in your company?</Typography>
                </ExpansionPanelSummary>
                <div styleName="rating">
                <UserRatingComponent fetchFeedbackCustomer={props.fetchFeedbackCustomer} width={"100%"}/>
                </div>
              </ExpansionPanel>
              <ExpansionPanel square expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
                <ExpansionPanelSummary aria-controls="panel3d-content" id="panel3d-header">
                  <Typography>How do you rate the catering?</Typography>
                </ExpansionPanelSummary>
                <div styleName="rating">
                <UserRatingComponent fetchFeedbackCustomer={props.fetchFeedbackCustomer} width={"100%"}/>
                </div>
              </ExpansionPanel>
              <ExpansionPanel square expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
                <ExpansionPanelSummary aria-controls="panel4d-content" id="panel4d-header">
                  <Typography>How do you value the invitation process / communication?</Typography>
                </ExpansionPanelSummary>
                <div styleName="rating">
                <UserRatingComponent fetchFeedbackCustomer={props.fetchFeedbackCustomer} width={"100%"}/>
                </div>
              </ExpansionPanel>
              <ExpansionPanel square expanded={expanded === 'panel5'} onChange={handleChange('panel5')}>
                <ExpansionPanelSummary aria-controls="panel5d-content" id="panel5d-header">
                  <Typography>How do you rate the overall organization of the event?</Typography>
                </ExpansionPanelSummary>
                <div styleName="rating">
                <UserRatingComponent fetchFeedbackCustomer={props.fetchFeedbackCustomer} width={"100%"}/>
                </div>
              </ExpansionPanel>
              <ExpansionPanel square expanded={expanded === 'panel6'} onChange={handleChange('panel6')}>
                <ExpansionPanelSummary aria-controls="panel6d-content" id="panel6d-header">
                  <Typography>How likely would you recommend HPE to friends and acquaintances? (10 = best grade)</Typography>
                </ExpansionPanelSummary>
                <div styleName="rating">
                <UserRatingComponent fetchFeedbackCustomer={props.fetchFeedbackCustomer} width={"100%"}/>
                </div>
              </ExpansionPanel>
              
              <ExpansionPanel square expanded={expanded === 'panel8'} onChange={handleChange('panel8')}>
                <ExpansionPanelSummary aria-controls="panel8d-content" id="panel8d-header">
                  <Typography>Would you like to be invited to the next innovation group (January 28, 2020 Bern / January 30, 2020 HPE Dbendorf)?</Typography>
                </ExpansionPanelSummary>
                <div styleName="rating">
                <UserRatingComponent fetchFeedbackCustomer={props.fetchFeedbackCustomer} width={"100%"}/>
                </div>
              </ExpansionPanel>
              <ExpansionPanel square expanded={expanded === 'panel9'} onChange={handleChange('panel9')}>
                <ExpansionPanelSummary aria-controls="panel9d-content" id="panel9d-header">
                  <Typography>How likely would you recommend this event to friends and acquaintances? (10 = best grade)</Typography>
                </ExpansionPanelSummary>
                <div styleName="rating">
                <UserRatingComponent fetchFeedbackCustomer={props.fetchFeedbackCustomer} width={"100%"}/>
                </div>
              </ExpansionPanel>
            </div>
          </div>
        );
      };

      export default applyWrappers(ExpansionComponent, styles);