import React, { Fragment } from "react";
import { connect } from "react-redux";
import SessionComponent from "../components/sessionComponent";
import updateRoute from '../../history';

class SessionContainer extends React.Component {
    componentDidMount() {
        if(!this.props.action.isLoggedIn){
            updateRoute({route: '/'});
        }
    }
    render() {
        return (
            <Fragment>
                <div className="base-app-box" >
                    {
                        <div className='app-body'>
                            <SessionComponent {...this.props} action={this.props.action}/>
                        </div>
                    }
                </div>
            </Fragment>
        );
    }
}
SessionContainer.propTypes = {};
SessionContainer.defaultProps = {};
const mapStateToProps = state => {
    return state
};
const mapDispatchToProps = dispatch => ({
});
export default connect(mapStateToProps, mapDispatchToProps)(SessionContainer);