import React from 'react';
import styles from './userRatingComponent.module.scss';
import applyWrappers from '../../wrappers/ComponentWrapper';
import Rating from '@material-ui/lab/Rating';

const UserRatingComponent = props => {
  const ratingWidth = { width: props.width};
  return (
    <div>
        <div>
          <span styleName="heading">User Rating for Speaker</span>
          <Rating name="read-only" value={4} readOnly />
        </div>
        <span styleName="fa checked" class="fa-star"></span>
        <span styleName="fa checked" class="fa-star"></span>
        <span styleName="fa checked" class="fa-star"></span>
        <span styleName="fa checked" class="fa-star"></span>
        <span styleName="fa " class="fa-star"></span>

        <p>4.1 average based on 254 reviews.</p>
        <div style={ratingWidth}>
        <div styleName="side">
          <div>5 star</div>
        </div>
        <div styleName="middle">
          <div styleName="bar-container" onClick={() => props.fetchFeedbackCustomer()}>
            <div styleName="bar-5"></div>
          </div>
        </div>
        <div styleName="side right">
          <div>150</div>
        </div>
        <div styleName="side">
          <div>4 star</div>
        </div>
        <div styleName="middle">
          <div styleName="bar-container" onClick={() => props.fetchFeedbackCustomer()}>
            <div styleName="bar-4"></div>
          </div>
        </div>
        <div styleName="side right">
          <div>63</div>
        </div>
        <div styleName="side">
          <div>3 star</div>
        </div>
        <div styleName="middle">
          <div styleName="bar-container" onClick={() => props.fetchFeedbackCustomer()}>
            <div styleName="bar-3"></div>
          </div>
        </div>
        <div styleName="side right">
          <div>15</div>
        </div>
        <div styleName="side">
          <div>2 star</div>
        </div>
        <div styleName="middle">
          <div styleName="bar-container" onClick={() => props.fetchFeedbackCustomer()}>
            <div styleName="bar-2"></div>
          </div>
        </div>
        <div styleName="side right">
          <div>6</div>
        </div>
        <div styleName="side">
          <div>1 star</div>
        </div>
        <div styleName="middle">
          <div styleName="bar-container" onClick={() => props.fetchFeedbackCustomer()}>
            <div styleName="bar-1"></div>
          </div>
        </div>
        <div styleName="side right">
          <div>20</div>
        </div>
      
      </div>

      { props.showUserRating &&
      
      <div styleName="topmargin" style={ratingWidth}>
        <p>User Rating for Content</p>
        <div styleName="side">
          <div>5 star</div>
        </div>
        <div styleName="middle">
          <div styleName="bar-container" onClick={() => props.fetchFeedbackCustomer()}>
            <div styleName="bar-5"></div>
          </div>
        </div>
        <div styleName="side right">
          <div>150</div>
        </div>
        <div styleName="side">
          <div>4 star</div>
        </div>
        <div styleName="middle">
          <div styleName="bar-container" onClick={() => props.fetchFeedbackCustomer()}>
            <div styleName="bar-4"></div>
          </div>
        </div>
        <div styleName="side right">
          <div>63</div>
        </div>
        <div styleName="side">
          <div>3 star</div>
        </div>
        <div styleName="middle">
          <div styleName="bar-container" onClick={() => props.fetchFeedbackCustomer()}>
            <div styleName="bar-3"></div>
          </div>
        </div>
        <div styleName="side right">
          <div>15</div>
        </div>
        <div styleName="side">
          <div>2 star</div>
        </div>
        <div styleName="middle">
          <div styleName="bar-container" onClick={() => props.fetchFeedbackCustomer()}>
            <div styleName="bar-2"></div>
          </div>
        </div>
        <div styleName="side right">
          <div>6</div>
        </div>
        <div styleName="side">
          <div>1 star</div>
        </div>
        <div styleName="middle">
          <div styleName="bar-container" onClick={() => props.fetchFeedbackCustomer()}>
            <div styleName="bar-1"></div>
          </div>
        </div>
        <div styleName="side right">
          <div>20</div>
        </div>
      
      </div>
}
    </div>
  );
}
export default applyWrappers(UserRatingComponent, styles);


