import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import styles from './cardDashboardComponent.module.scss';
import applyWrappers from '../../wrappers/ComponentWrapper';
import img from './img.jpg';
import zurich from './zurich.jpg';

const CardDashboardComponent = props => {

  return (
    <div styleName="card">
    <img src={props.image == "1" ? img : zurich } alt="City" styleName="img"/>
  
  </div>
  );
}
export default applyWrappers(CardDashboardComponent, styles);