import React from "react";
import _ from "lodash";
import styles from "./sessionComponent.module.scss";
import applyWrappers from "../../wrappers/ComponentWrapper";
import * as FileSaver from "file-saver";
import * as XLSX from "xlsx";
import SidebarComponent from "../sidebarComponent";
import { ResponsivePie } from "@nivo/pie";
const convert = require('xml-js');

const SessionComponent = props => {
  let set1 = [],
    set2 = [],
    set3 = [];
  let users = {};
  let time1 = props.action.city == "be" ? 13 : 12;
  let customerType = [];

  function isEmpty(obj) {
    for (var prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        return false;
      }
    }

    return JSON.stringify(obj) === JSON.stringify({});
  }
  if (props.action.registeredUsers) {
    //const data0 = typeof props.action.registeredUsers == "string" ? JSON.parse(props.action.registeredUsers) :  props.action.registeredUsers;
    let data0 = props.action.registeredUsers;
    data0 = JSON.parse(convert.xml2json(data0.documentElement.innerHTML,{compact: true,  spaces: 4}));
    const registeredUsers =
      data0["soap:Body"][
        "LoadAllRegisteredUsersOfActiveEditionResponse"
      ]["LoadAllRegisteredUsersOfActiveEditionResult"]["diffgr:diffgram"][
        "DocumentElement"
      ]["tblOutput"];
    registeredUsers.filter(eachUser => {
      if (!isEmpty(eachUser["UserSessions_IDs"])) {
        let userSessions = eachUser["UserSessions_IDs"]["_text"];
        let sessions;

        if (customerType.indexOf(eachUser["UserType_Title"]["_text"]) == -1) {
          customerType.push(eachUser["UserType_Title"]["_text"]);
        }
        if (userSessions) {
          sessions = userSessions.split("|");
          for (let i = 0; i < sessions.length; i++) {
            if (sessions[i] in users) {
              users[sessions[i]].push(eachUser);
            } else {
              users[sessions[i]] = [];
              users[sessions[i]].push(eachUser);
            }
          }
        }
      }
    });
  }
  if (!isEmpty(props.action.sessionData)) {
    let data1 = props.action.sessionData;
    data1 = JSON.parse(convert.xml2json(data1.documentElement.innerHTML,{compact: true,  spaces: 4}));
    if (data1["soap:Body"]) {
      const d1 =
        data1["soap:Body"][
          "LoadAllSessionsOfActiveEditionResponse"
        ]["LoadAllSessionsOfActiveEditionResult"]["diffgr:diffgram"][
          "DocumentElement"
        ]["tblOutput"];
      d1.filter(item => {
        if (item["Session_MaxParticipants"]["_text"]) {
          if (new Date(item["TimeSlot_DateFrom"]["_text"]).getHours() == 11) {
            let data = {};
            data.item = item;
            const filterusers = _.pickBy(users, function(v, k) {
              return k == item["Session_ID"]["_text"];
            });
            data.users = filterusers[item["Session_ID"]["_text"]];
            data.customerType = [];
            for (let i = 0; i < customerType.length; i++) {
              let type = customerType[i];
              var values = _.filter(data.users, function(user) {
                if (user["UserType_Title"]["_text"] == type) {
                  return user;
                }
              });

              data.customerType.push({ type, value: values.length });
            }

            var mitarbeiter = _.filter(data.users, function(user) {
              if (user["UserType_Title"]["_text"] == "HPE Mitarbeiter") {
                return user;
              }
            });
            data.mitarbeiter = mitarbeiter.length;
            set1.push(data);
          } else if (
            new Date(item["TimeSlot_DateFrom"]["_text"]).getHours() == time1
          ) {
            let data = {};
            data.item = item;
            const filterusers = _.pickBy(users, function(v, k) {
              return k == item["Session_ID"]["_text"];
            });
            data.users = filterusers[item["Session_ID"]["_text"]];
            data.customerType = [];
            for (let i = 0; i < customerType.length; i++) {
              let type = customerType[i];
              var values = _.filter(data.users, function(user) {
                if (user["UserType_Title"]["_text"] == type) {
                  return user;
                }
              });
              data.customerType.push({ type, value: values.length });
            }

            var mitarbeiter = _.filter(data.users, function(user) {
              if (user["UserType_Title"]["_text"] == "HPE Mitarbeiter") {
                return user;
              }
            });
            data.mitarbeiter = mitarbeiter.length;
            set2.push(data);
          } else if (
            new Date(item["TimeSlot_DateFrom"]["_text"]).getHours() == 14
          ) {
            let data = {};
            data.item = item;
            const filterusers = _.pickBy(users, function(v, k) {
              return k == item["Session_ID"]["_text"];
            });
            data.users = filterusers[item["Session_ID"]["_text"]];
            data.customerType = [];
            for (let i = 0; i < customerType.length; i++) {
              let type = customerType[i];
              var values = _.filter(data.users, function(user) {
                if (user["UserType_Title"]["_text"] == type) {
                  return user;
                }
              });

              data.customerType.push({ type, value: values.length });
            }

            var mitarbeiter = _.filter(data.users, function(user) {
              if (user["UserType_Title"]["_text"] == "HPE Mitarbeiter") {
                return user;
              }
            });
            data.mitarbeiter = mitarbeiter.length;
            set3.push(data);
          }
        }
      });
    }
  }

  //to export
  const fileType =
    "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
  const fileExtension = ".xlsx";

  const exportToCSV = (csvData, fileName) => {
    const ws = XLSX.utils.json_to_sheet(csvData);
    const wb = { Sheets: { data: ws }, SheetNames: ["data"] };
    const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" });
    const data = new Blob([excelBuffer], { type: fileType });
    FileSaver.saveAs(data, fileName + fileExtension);
  };

  const colors = ["#425563", "#F4A742", "#80746E", "#00B388"];

  const renderAllcard = data => {
    let finalData = [];
    let obj;
    if (data.length > 0) {
      for (let i = 0; i < data.length; i++) {
        obj = renderCard(data[i],i);
        finalData.push(obj);
      }
    }
    return finalData;
  };

  const getData = eachData => {
    let data = [];
    if (eachData && eachData.customerType) {
      let types = eachData.customerType;
      for (let i = 0; i < types.length; i++) {
        let obj1 = {};
        obj1.id = types[i].type;
        obj1.label = types[i].type;
        obj1.value = types[i].value;
        obj1.color = colors[i];
        data.push(obj1);
      }
    }
    return data;
  };

  const renderCard = (eachData,index) => {
    //let allUsers = eachData.users;
    let userLength = eachData ? eachData.users ? eachData.users.length : 0 : 0;
    let users_data = [];
    if (eachData.users) {
      for (let i = 0; i < userLength; i++) {
        users_data.push({
          FirstName: `${eachData.users[i]["FirstName"]["_text"]}`,
          LastName: `${eachData.users[i]["LastName"]["_text"]}`,
          Email: `${eachData.users[i]["Email"]["_text"]}`,
          InvitationCode: `${eachData.users[i]["InvitationCode"]["_text"]}`,
          Salutation_Title: `${eachData.users[i]["Salutation_Title"]["_text"]}`,
          CompanyName: `${eachData.users[i]["CompanyName"]["_text"]}`,
          MobilePhone: `${eachData.users[i]["MobilePhone"]["_text"]}`,
          UserSessions_IDs: `${eachData.users[i]["UserSessions_IDs"]["_text"]}`,
          Organizer_InvitedBy: `${eachData.users[i]["Organizer_InvitedBy"]["_text"]}`,
          UserType_Title: `${eachData.users[i]["UserType_Title"]["_text"]}`,
          Function_Title: `${eachData.users[i]["Function_Title"]["_text"]}`
        });
      }
    }
    return (
      // <Card styleName="card">
      <div key={index} styleName="pie1">
        <div styleName="title">
          {eachData && eachData.item["Session_Title"]["_text"]}
        </div>
        <div styleName="pie">
          <ResponsivePie
            data={getData(eachData)}
            height={250}
            margin={{ top: 0, right: 0, bottom: 20, left: 20 }}
            innerRadius={0.4}
            cornerRadius={1}
            colors={colors}
            borderWidth={1}
            borderColor={{ from: "color", modifiers: [["darker", 0.2]] }}
            enableRadialLabels={false}
            radialLabelsSkipAngle={10}
            radialLabelsTextXOffset={6}
            radialLabelsTextColor="#333333"
            radialLabelsLinkOffset={0}
            radialLabelsLinkDiagonalLength={16}
            radialLabelsLinkHorizontalLength={24}
            radialLabelsLinkStrokeWidth={1}
            radialLabelsLinkColor={{ from: "color" }}
            slicesLabelsSkipAngle={10}
            slicesLabelsTextColor="#333333"
            animate={true}
            motionStiffness={90}
            motionDamping={15}
            isInteractive={true}
            fill={[
              {
                match: {
                  id: "ruby"
                },
                id: "dots"
              },
              {
                match: {
                  id: "c"
                },
                id: "dots"
              },
              {
                match: {
                  id: "go"
                },
                id: "dots"
              },
              {
                match: {
                  id: "python"
                },
                id: "dots"
              },
              {
                match: {
                  id: "scala"
                },
                id: "lines"
              },
              {
                match: {
                  id: "lisp"
                },
                id: "lines"
              },
              {
                match: {
                  id: "elixir"
                },
                id: "lines"
              },
              {
                match: {
                  id: "javascript"
                },
                id: "lines"
              }
            ]}
            legends={[
              {
                dataFrom: "keys",
                anchor: "right",
                direction: "column",
                justify: false,
                translateX: 105,
                translateY: 0,
                itemsSpacing: 2,
                itemWidth: 100,
                itemHeight: 20,
                itemDirection: "left-to-right",
                overflowWrap: "break-word",
                itemOpacity: 0.85,
                symbolSize: 20,
                effects: [
                  {
                    on: "hover",
                    style: {
                      itemOpacity: 1
                    }
                  }
                ]
              }
            ]}
          />
        </div>
        <div styleName="session_statistics">
          <table>
            <tbody>
              <tr>
                <td>Max. participants:</td>
                <td>
                  {eachData &&
                    eachData.item["Session_MaxParticipants"]["_text"]}
                </td>
              </tr>
              <tr>
                <td>Number of participants:</td>
                <td>{userLength}</td>
              </tr>
              <tr>
                <td>Thereof HPE-Employees:</td>
                <td>{eachData && eachData.mitarbeiter}</td>
              </tr>
              <tr>
                <td>Available places</td>
                <td>
                  {eachData &&
                    eachData.item["Session_MaxParticipants"]["_text"] -
                    userLength}
                </td>
              </tr>
              <tr
                styleName="link"
                onClick={e => exportToCSV(users_data, "users")}
              >
                <td>
                  <a>Participants</a>
                </td>
                <td></td>
              </tr>
            </tbody>
          </table>
        </div>
        {/* </Card> */}
      </div>
    );
  };
  return (
    <div>
      <SidebarComponent active="session" />
      <div styleName="subcontainer">
        <div>
          <h2 styleName="heading">HPE Innovation Circle</h2>
          <h3 styleName="sub-title">
            { props.action.city == "be" ? "Bern | 2. April 2020" : "Dbendorf | 7. April 2020" }
          </h3>
          <div styleName="title1">Sessions evaluation</div>
          <div styleName="sessions_legend">
            <b>Legend of the User-Typen</b>
            <div>
              <span styleName="kunde"></span>
              Kunde
            </div>
            <div>
              <span styleName="partner"></span>
              HPE Partner
            </div>
            <div>
              <span styleName="sponser"></span>
              Sponsor
            </div>
            <div styleName="space">
              <span styleName="mitarbeiter"></span>
              HPE Mitarbeiter
            </div>
          </div>
          <mark styleName="timeslot_title">{ props.action.city == "be" ?'11:00 - 12:00' : '11:00 - 11:50'}</mark>
          <div styleName="container">{renderAllcard(set1)}</div>
          <hr></hr>
          <mark styleName="timeslot_title">{ props.action.city == "be" ? '13:00 - 13:50' : '12:00 - 13:00'}</mark>
          <div styleName="container">{renderAllcard(set2)}</div>
          <hr></hr>
          <mark styleName="timeslot_title">{ props.action.city == "be" ? '14:00 - 14:50' : '14:00 - 14:50'}</mark>
          <div styleName="container">{renderAllcard(set3)}</div>
          <hr></hr>
        </div>
      </div>
    </div>
  );
};
export default applyWrappers(SessionComponent, styles);
