const initialState = {
    city: 'be',
    isLoggedIn : false,
    showRating : false,
    showFeedbackSession : true,
    showFeedbackCustomer : false,
    showCustomerData: false,
    showCustomerFunction: false,
    showNoFeedback: false,
    showExpansionPanel: false,
    showSalesFeedback: false,
    sessionData : {}
}

const action = (state = initialState , action = {}) =>{

    switch(action.type){
        case "login_user":
            return { 
                ...state,
                isLoggedIn : action.payload
            };

        case "set_city_be":
            return { 
                ...state,
                city : "be"
            };

        case "set_city_zh":
            return { 
                ...state,
                city : "zh"
            };

        case "session_data":
            return { 
                ...state,
                sessionData : action.payload
            };

        case "registered_users":
            return{
                ...state,
                registeredUsers: action.payload
           }
        case "show_rating":
            return {
                ...state,
                showRating: true,
                showFeedbackSession: false,
                showFeedbackCustomer : false,
                showNoFeedback: false,
                showExpansionPanel: false,
                showSalesFeedback: false
            }
        case "show_feedback_session":
            return {
                ...state,
                showRating: false,
                showFeedbackSession : true,
                showFeedbackCustomer : false,
                showNoFeedback: false,
                showExpansionPanel: false,
                showSalesFeedback: false
            }
        case "show_feedback_customer":
            return {
                ...state,
                showRating: false,
                showFeedbackSession : false,
                showNoFeedback: false,
                showExpansionPanel: false,
                showSalesFeedback: false,
                showFeedbackCustomer : true
            }
        case "show_no_feedback":
            return {
                ...state,
                showNoFeedback: true,
                showExpansionPanel: false,
                showRating: false,
                showFeedbackSession : false,
                showSalesFeedback: false,
                showFeedbackCustomer : false
            }

        case "show_expansion_panel":
            return {
                ...state,
                showExpansionPanel: true,
                showNoFeedback : false,
                showRating: false,
                showFeedbackSession : false,
                showSalesFeedback: false,
                showFeedbackCustomer : false
            }
        case "show_sales_panel":
            return {
                ...state,
                showExpansionPanel: false,
                showNoFeedback : false,
                showRating: false,
                showFeedbackSession : false,
                showSalesFeedback: true,
                showFeedbackCustomer : false
            }
        case "show_customer_data":
            return {
                ...state,
                showCustomerData: true,
                showCustomerFunction: false
            }
        case "show_customer_function":
            return {
                ...state,
                showCustomerData: false,
                showCustomerFunction: true
            }
        default:
            return state;
    }
}

export default action;