import React from 'react';
import styles from './customerComponent.module.scss';
import applyWrappers from '../../wrappers/ComponentWrapper';
import SidebarComponent from "../sidebarComponent";
import PieListComponent from "../pieListComponent";
import _ from 'lodash';
const convert = require('xml-js');

const CustomerComponent = (props) => {
    let customerType = [];
    let functionTitles = []
    let groupByData = [];

    if(props.action.registeredUsers){
        let data0 = props.action.registeredUsers;
        data0 = JSON.parse(convert.xml2json(data0.documentElement.innerHTML,{compact: true,  spaces: 4}));
        const registeredUsers = data0['soap:Body']['LoadAllRegisteredUsersOfActiveEditionResponse']['LoadAllRegisteredUsersOfActiveEditionResult']['diffgr:diffgram']['DocumentElement']['tblOutput'];
        registeredUsers.filter(eachUser => {
            if((customerType.indexOf(eachUser['UserType_Title']['_text']) == -1)){
            customerType.push(eachUser['UserType_Title']['_text']);
            }
            if((functionTitles.indexOf(eachUser['Function_Title']['_text']) == -1)){
                functionTitles.push(eachUser['Function_Title']['_text']);
            }
        });

        for(let i=0 ; i < functionTitles.length ; i++){
            let functionTitle = functionTitles[i];
            var users = _.filter(registeredUsers, function(user) {
              if (user['Function_Title']['_text'] == functionTitle) {
                return user;
              }
            });
            let userByType = [];
            for(let j=0 ; j < customerType.length ; j++){     
                let typeName = customerType[j];
                var usersByType = _.filter(users, function(user) {
                  if (user['UserType_Title']['_text'] == typeName) {
                    return user;
                  }
                });
                userByType.push({typeName , typeLen: usersByType.length })
            }
            groupByData.push({functionTitle, len: users.length,userByType});
        }
    }
    
    return (
        <div>
        <SidebarComponent active="customer"/>
        <div styleName="subcontainer">
            <div>
                <h2 styleName="title">
                    
                    HPE Innovation Circle
                </h2>
                <h3 styleName="sub-title">
                    
                {props.action.city == "be" ?  "Bern | 2. April 2020" : "Dbendorf | 7. April 2020"}
                </h3>
                <div styleName="title1">Company sizes per function per day</div>
                <PieListComponent groupByData={groupByData}/>
            </div>
        </div>
    </div>	
    );
}
export default applyWrappers(CustomerComponent, styles);