import React, { Component } from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import LoginContainer from '../containers/LoginContainer';
import DashboardContainer from '../containers/DashboardContainer';
import RegistrationContainer from '../containers/RegistrationContainer';
import CustomerContainer from '../containers/CustomerContainer';
import EntranceContainer from '../containers/EntranceContainer';
import SessionContainer from '../containers/SessionContainer';
import CardListContainer from '../containers/CardListContainer';

class AppRouter extends Component {
    render() {
        return (
            <Router>
                <Switch>
                    <Route exact path='/' component={LoginContainer} />
                    <Route exact path='/login' component={LoginContainer} />
                    <Route exact path='/dashboard' component={DashboardContainer} />
                    <Route exact path='/registration' component={RegistrationContainer} />
                    <Route exact path='/customer' component={CustomerContainer} />
                    <Route exact path='/entrance' component={EntranceContainer} />
                    <Route exact path='/session' component={SessionContainer} />
                    <Route exact path='/feedback' component={CardListContainer} />
                </Switch>
            </Router>
        )
    }
}
export default AppRouter;