import React from 'react';
import styles from './dashboardComponent.module.scss';
import applyWrappers from '../../wrappers/ComponentWrapper';
import SidebarComponent from "../sidebarComponent";
import CardDashboardComponent from "../cardDashboardComponent"
import wall from './wall.jpg';
const DashboardComponent = (props) => {
    var sectionStyle = {
        width: "100%",
  height: "400px",
  backgroundImage: "url(" + { wall } + ")"
      };
    return (
        <div>
            <SidebarComponent active="dashboard"/>
            <div styleName="subcontainer">
            <h2 styleName="title"></h2>
            <h2 stylename="subtitle1">ADMIN DASHBOARD</h2>
                <div>
                    <div styleName="content">
                    <img src={wall} height="550" width="1250"></img>
                    </div>
                </div>
            </div>
            
        </div>
    );
}
export default applyWrappers(DashboardComponent, styles);