import React from "react";
import styles from "./tableDataComponent.module.scss";
import applyWrappers from "../../wrappers/ComponentWrapper";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";

const TableDataComponent = props => {
  const columns = [
    {
      id: "Salutation",
      label: "Salutation",
      minWidth: 30,
      align: "center",
      format: value => value.toLocaleString()
    },
    {
      id: "FirstName",
      label: "First Name",
      minWidth: 100,
      align: "center",
      format: value => value.toLocaleString()
    },
    {
      id: "LastName",
      label: "Last Name",
      minWidth: 100,
      align: "center",
      format: value => value.toLocaleString()
    },
    {
      id: "Company",
      label: "Company",
      minWidth: 170,
      align: "center",
      format: value => value.toLocaleString()
    },
    {
      id: "Function",
      label: "Function",
      minWidth: 170,
      align: "center",
      format: value => value.toLocaleString()
    }
  ];

  function createData(Salutation, FirstName, LastName, Company, Function) {
    return { Salutation, FirstName, LastName, Company, Function };
  }

  const rows = [
    createData(
      "Her",
      "Sakshi",
      "Poddar",
      "General Dynamics European Land Systems-Mowag",
      "IT"
    ),
    createData(
      "Her",
      "Sakshi",
      "Poddar",
      "General Dynamics European Land Systems-Mowag",
      "IT"
    ),
    createData(
      "Her",
      "Sakshi",
      "Poddar",
      "General Dynamics European Land Systems-Mowag",
      "IT"
    ),
    createData(
      "Her",
      "Sakshi",
      "Poddar",
      "General Dynamics European Land Systems-Mowag",
      "IT"
    ),
    createData(
      "Her",
      "Sakshi",
      "Poddar",
      "General Dynamics European Land Systems-Mowag",
      "IT"
    ),
    createData(
      "Her",
      "Sakshi",
      "Poddar",
      "General Dynamics European Land Systems-Mowag",
      "IT"
    ),
    createData(
      "Her",
      "Sakshi",
      "Poddar",
      "General Dynamics European Land Systems-Mowag",
      "IT"
    ),
    createData(
      "Her",
      "Sakshi",
      "Poddar",
      "General Dynamics European Land Systems-Mowag",
      "IT"
    ),
    createData(
      "Her",
      "Sakshi",
      "Poddar",
      "General Dynamics European Land Systems-Mowag",
      "IT"
    ),
    createData(
      "Her",
      "Sakshi",
      "Poddar",
      "General Dynamics European Land Systems-Mowag",
      "IT"
    )
  ];

  const useStyles = makeStyles({
    root: {
      width: "100%"
    },
    container: {
      maxHeight: 440
    }
  });
  const classes = useStyles();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  return (
    <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              {columns.map(column => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth }}
                >
                  {column.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map(row => {
                return (
                  <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                    {columns.map(column => {
                      const value = row[column.id];
                      return (
                        <TableCell key={column.id} align={column.align}>
                          {column.format && typeof value === "number"
                            ? column.format(value)
                            : value}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[10, 25, 100]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
    </Paper>
  );
};
export default applyWrappers(TableDataComponent, styles);
