import { 
    initialCityBe,
    initialCityZh,
    initialRatingData , 
    initialFeedbackSessionData,
    initialFeedbackCustomer,
    initialCustomerData,
    initialCustomerFunction,
    initialNoFeedbackData,
    initialExpansionPanel,
    initialSalesFeedbackData,
    initialSessionData,
    initialRegisteredUsers,
    initialLogin
   } from '../actionApi';
const axios = require('axios');
const convert = require('xml-js');

let city = "zh" // "be"
let parser = new DOMParser();


export const saveLogin = (value) =>  (dispatch) => {
   dispatch(initialLogin(value))
}

export const fetchCityBe = () =>  (dispatch) => {
   city = "be";
   dispatch(initialCityBe('hello'))
}

export const fetchCityZh = () =>  (dispatch) => {
   city = "zh";
   dispatch(initialCityZh('hello'))
}

export const fetchRegisteredUsers = () => (dispatch) =>{
   const xmlBodyStr = `<?xml version="1.0" encoding="utf-8"?>
   <soap:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/">
     <soap:Body>
       <LoadAllRegisteredUsersOfActiveEdition xmlns="http://tempuri.org/">
         <email>hpenterprise@ticketino.com</email>
         <password>inn0Vat!20</password>
       </LoadAllRegisteredUsersOfActiveEdition>
     </soap:Body>
   </soap:Envelope>
   `;
   var config = {
    headers: {
        'Content-Type': 'text/xml',
   }
   };

   axios.post('https://hpe-2020-'+city+'-1.ticketino.com/WebServices/WSHPInvent.asmx?op=LoadAllSessionsOfUser', xmlBodyStr,config).then(res => {
       let parsedData =  parser.parseFromString(res.data,"text/xml");
       dispatch(initialRegisteredUsers(parsedData));
       }).catch(err => 
        console.log(err)
       )}

   
