import React from "react";
import styles from "./salesFeedbackComponent.module.scss";
import applyWrappers from "../../wrappers/ComponentWrapper";
import TableDataComponent from "../tableDataComponent";

const SalesFeedbackComponent = props => {
  return (
    <div>
        <div>
          <h2 styleName="title">HPE Innovation Circle</h2>
          <h3 styleName="sub-title1">{ props.action.city == "be" ? "Bern | 2. April 2020" : "Dbendorf | 7. April 2020" }</h3>
          <h3 styleName="sub-title">Feedback for Sales</h3>
          <table styleName="partner">
            <tr>
              <td>Participants Planning a New Project:</td>
              <td>
                <select
                  name="ctl00$ContentPlaceHolder1$ddlOrganizers"
                  onchange="javascript:setTimeout(&#39;__doPostBack(\&#39;ctl00$ContentPlaceHolder1$ddlOrganizers\&#39;,\&#39;\&#39;)&#39;, 0)"
                  id="ContentPlaceHolder1_ddlOrganizers"
                  styleName="all"
                >
                  <option selected="selected" value="">
                    Alle
                  </option>
                  <option value="29">Yes</option>
                  <option value="114">No</option>
                </select>
              </td>
            </tr>
            <tr>
              <td>Planned Budget:</td>
              <td>
                <select
                  name="ctl00$ContentPlaceHolder1$ddlDays"
                  onchange="javascript:setTimeout(&#39;__doPostBack(\&#39;ctl00$ContentPlaceHolder1$ddlDays\&#39;,\&#39;\&#39;)&#39;, 0)"
                  id="ContentPlaceHolder1_ddlDays"
                  styleName="selected"
                >
                  <option selected="selected" value="">
                    Alle
                  </option>
                  <option value="11">Less than 50000</option>       
                  <option value="12">50000-150000</option>
                  <option value="13">150000-250000</option>
                  <option value="14">Less than 250000</option>
                  <option value="15">No Defined Budget</option>
                </select>
              </td>
            </tr>
            <tr>
              <td>Timescale:</td>
              <td>
                <select
                  name="ctl00$ContentPlaceHolder1$ddlDays"
                  onchange="javascript:setTimeout(&#39;__doPostBack(\&#39;ctl00$ContentPlaceHolder1$ddlDays\&#39;,\&#39;\&#39;)&#39;, 0)"
                  id="ContentPlaceHolder1_ddlDays"
                  styleName="selected"
                >
                  <option selected="selected" value="">
                    Alle
                  </option>
                  <option value="21">Less than01 Month</option>       
                  <option value="22">01-03 Months</option>
                  <option value="23">03-05 Months</option>
                  <option value="24">06-12 Months</option>
                  <option value="25">Less than 1 Year</option>
                  <option value="26">Not Defined</option>
                </select>
              </td>
            </tr>
            <tr>
              <td>Role:</td>
              <td>
                <select
                  name="ctl00$ContentPlaceHolder1$ddlDays"
                  onchange="javascript:setTimeout(&#39;__doPostBack(\&#39;ctl00$ContentPlaceHolder1$ddlDays\&#39;,\&#39;\&#39;)&#39;, 0)"
                  id="ContentPlaceHolder1_ddlDays"
                  styleName="selected"
                >
                  <option selected="selected" value="">
                    Alle
                  </option>
                  <option value="31">Business Sponsor</option>       
                  <option value="32">Buyer</option>
                  <option value="33">Deciscion Maker</option>
                  <option value="34">Evaluator/Recommender</option>
                  <option value="35">Influencer</option>
                  <option value="36">Other Job Desciption</option>
                </select>
              </td>
            </tr>
            <tr>
              <td>Number of search results:</td>
              <td>0</td>
            </tr>
            <tr>
              <td></td>
              <td>
                <input
                  type="submit"
                  name="ctl00$ContentPlaceHolder1$ButtonExport"
                  value="Show"
                  styleName="showbtn"
                  id="ContentPlaceHolder1_ButtonExport"
                />
              </td>
            </tr>
          </table>
          <div>
            <table
              class="grid"
              cellspacing="0"
              id="ContentPlaceHolder1_gvUsers"
              styleName="grid"
            >
              <tr>
                <td colspan="6">No matching users found.</td>
              </tr>
            </table>
            <div styleName="tableData">            
              <TableDataComponent/>
            </div> 
          </div>
          <br />
        </div>
      </div>
  );
};
export default applyWrappers(SalesFeedbackComponent, styles);
