import React, { Fragment } from "react";
import { connect } from "react-redux";
import CardListComponent from "../components/cardListComponent";
import updateRoute from '../../history';
import { fetchRatingData , fetchFeedbackSession , fetchFeedbackCustomer } from "../actions/dispatchAction/fetchData";

class CardListContainer extends React.Component {
    componentDidMount() {
        if(!this.props.action.isLoggedIn){
            updateRoute({route: '/'});
        }
        this.props.fetchFeedbackSession();
    }
     
    fetchFeedbackSession = () => {
        this.props.fetchFeedbackSession();
    };
    fetchRatingData = () => {
        this.props.fetchRatingData();
      };
    fetchFeedbackCustomer = () => {
        this.props.fetchFeedbackCustomer();
    };
    render() {
        return (                       
        <CardListComponent {...this.props} 
            fetchRatingData={this.fetchRatingData} 
            fetchFeedbackSession={this.fetchFeedbackSession} 
            fetchFeedbackCustomer= {this.props.fetchFeedbackCustomer} 
        />                
        )

    }
    }
CardListContainer.propTypes = {};
CardListContainer.defaultProps = {};
const mapStateToProps = state => {
    return state
};
const mapDispatchToProps = dispatch => ({
    fetchRatingData : () => {
        dispatch(fetchRatingData())
    },

    fetchFeedbackSession : () => {
        dispatch(fetchFeedbackSession())
    },

    fetchFeedbackCustomer: () => {
        dispatch(fetchFeedbackCustomer())
    }

});
export default connect(mapStateToProps, mapDispatchToProps)(CardListContainer);