import React, { Fragment } from "react";
import { connect } from "react-redux";
//import DesktopHeader from "components/header/desktopHeader";
import CustomerComponent from "../components/customerComponent";
import PieByFunctionsComponent from "../components/pieByFunctionsComponent";
import updateRoute from '../../history';

class CustomerContainer extends React.Component {
    componentDidMount() {
        if(!this.props.action.isLoggedIn){
            updateRoute({route: '/'});
        }
    }
    render() {
        return (
            <Fragment>
                <div className="base-app-box" >
                    {/* <DesktopHeader
                        {...this.props}
                    /> */}
                    {
                        <div className='app-body'>
                            {this.props.action.showCustomerData ? <CustomerComponent {...this.props}/>:<PieByFunctionsComponent {...this.props}/>}
                        </div>
                    }
                </div>
            </Fragment>
        );
    }
}
CustomerContainer.propTypes = {};
CustomerContainer.defaultProps = {};
const mapStateToProps = state => {
    return state
};
const mapDispatchToProps = dispatch => ({
});
export default connect(mapStateToProps, mapDispatchToProps)(CustomerContainer);