import React from 'react';
import styles from './cardListComponent.module.scss';
import applyWrappers from '../../wrappers/ComponentWrapper';
import SidebarComponent from "../sidebarComponent";
import CardComponent from "../cardComponent";
import UserRatingComponent from "../userRatingComponent";
import FeedbackCustomerListComponent from "../feedbackCustomerListComponent";
import NoFeedbackComponent from "../noFeedbackComponent";
import SalesFeedbackComponent from "../salesFeedbackComponent";
import ExpansionComponent from "../expansionComponent";

const CardListComponent = (props) => {
    return (
        <div>
        <SidebarComponent active="feedback"/>
        <div styleName="mainContainer">
        {props.action.showSalesFeedback ? <SalesFeedbackComponent {...props} /> : ""}
        {props.action.showNoFeedback ? <NoFeedbackComponent {...props}/> : ""}
        {props.action.showExpansionPanel ? <ExpansionComponent {...props} fetchFeedbackCustomer={props.fetchFeedbackCustomer}/> : ""}
        { props.action.showFeedbackSession ? <div>
          <h2 styleName="title">HPE Innovation Circle</h2>
          <div styleName="sub-title1">
          {props.action.city == "be" ?  "Bern | 2. April 2020" : "Dbendorf | 7. April 2020"}
            </div>
          <h3 styleName="sub-title">Feedback for the Session</h3>
        <span styleName="timeslot_title">                           
            11:00 - 11:50
          </span>
        <div styleName="subcontainer">
           <CardComponent name="Session 1" fetchRatingData={props.fetchRatingData}/>
           <CardComponent name="Session 2" fetchRatingData={props.fetchRatingData}/>
           <CardComponent name="Session 3" fetchRatingData={props.fetchRatingData}/>
           <CardComponent name="Session 4" fetchRatingData={props.fetchRatingData}/>
        </div>
        <span styleName="timeslot_title">                           
            12:00 - 13:00
          </span>
        <div styleName="subcontainer">
           <CardComponent name="Session 5" fetchRatingData={props.fetchRatingData}/>
           <CardComponent name="Session 6" fetchRatingData={props.fetchRatingData}/>
           <CardComponent name="Session 7" fetchRatingData={props.fetchRatingData}/>
           <CardComponent name="Session 8" fetchRatingData={props.fetchRatingData}/>
        </div>
        <span styleName="timeslot_title">                           
            14:00 - 14:50
          </span>
        <div styleName="subcontainer">
           <CardComponent name="Session 9" fetchRatingData={props.fetchRatingData}/>
           <CardComponent name="Session 10" fetchRatingData={props.fetchRatingData}/>
           <CardComponent name="Session 11" fetchRatingData={props.fetchRatingData}/>
           <CardComponent name="Session 12" fetchRatingData={props.fetchRatingData}/>
        </div> </div> : "" }
        { props.action.showRating ? <UserRatingComponent showUserRating={true} {...props} width={"66%"} fetchFeedbackCustomer={props.fetchFeedbackCustomer}/> : "" }

        { props.action.showFeedbackCustomer ? <FeedbackCustomerListComponent {...props} /> : "" }
        </div>
    </div>	
    );
}
export default applyWrappers(CardListComponent, styles);