import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import styles from './loginComponent.module.scss';
import applyWrappers from '../../wrappers/ComponentWrapper';
import logo from './logo.png';
import logo2 from './logo2.jpg';
import bground from './bground.jpg';
import updateRoute from '../../../history';

const LoginComponent = (props) => {

    const useStyles = makeStyles(theme => ({
        formControl: {
            minWidth: '110px',
            marginTop: '-30px',
            marginBttom: '37px',
            marginLeft: '55px'
        },
        selectEmpty: {
          marginTop: theme.spacing(0),
        },
      }));
    const classes = useStyles();

    const [state, setState] = React.useState({
        city: 'be',
        name: 'hai',
        username: '',
        password: ''
    });

    const updateName = (evt) =>{
        setState({
            ...state,
            username: evt.target.value
        });
    }
    
    const updatePassword = (evt) =>{
        setState({
            ...state,
            password: evt.target.value
        });
    }

    const handleChange = (event) => {
        //const city = event.target.value == ""
        setState({
          ...state,
          city: event.target.value,
        });
      };

    const onLoginClick = (ele) => {
        if(state.username == "admin" && state.password == "admin@123"){
            props.saveLogin();
            if(state.city == "be"){
                props.fetchCityBe();
            }else{
                props.fetchCityZh();
            }
            updateRoute({route: '/'+ele});
        }else if(state.username && state.password){
            alert("Please provide valid credentials");
        }else{
            alert("Please enter username and password");
        }
        
    };
    

    var sectionStyle = {
        backgroundImage: `url(${ bground })`,
        backgroundSize:"cover",
        backgroundRepeat:"no-repeat"
      };
    return (
        <div style={sectionStyle}>
            <img src={logo} styleName="logo"></img>
                <div styleName="formcontainer"> <img styleName="img1" src={logo2}></img>
                    <p styleName="text">ADMIN LOGIN</p> 
                    <div styleName="center">
                    <input value={state.email} onChange={updateName} styleName="email" type="email" type="tel" placeholder="Email"/>
                    <input  value={state.password} onChange={updatePassword} styleName="password" type="password" placeholder="Password"/>
                    {/* <input styleName="check" type="checkbox"></input> */}
                    {/* <p styleName="text1">Remember Me</p> */}
                        <FormControl className={classes.formControl}>
                         <InputLabel id="demo-simple-select-label">City</InputLabel>
                        <Select
                        native
                        value={state.city}
                        onChange={handleChange}
                        inputProps={{
                            name: 'city',
                            id: 'filled-age-native-simple',
                        }}
                        >
                        <option value={"be"}>Bern</option>
                        <option value={"zh"}>Zrich</option>
                        </Select>
                    </FormControl>
                     <button styleName="login" type="submit" onClick={() => onLoginClick("dashboard")}>Log In</button>
                    </div>
                </div>
        </div>
    );
}
export default applyWrappers(LoginComponent, styles);