import React, { Fragment } from "react";
import { connect } from "react-redux";
//import DesktopHeader from "components/header/desktopHeader";
import EntranceComponent from "../components/entranceComponent";
import updateRoute from '../../history';

class EntranceContainer extends React.Component {
    componentDidMount() {
        if(!this.props.action.isLoggedIn){
            updateRoute({route: '/'});
        }
    }
    render() {
        return (
            <Fragment>
                <div className="base-app-box" >
                    {
                        <div className='app-body'>
                            <EntranceComponent {...this.props}
                            />
                        </div>
                    }
                </div>
            </Fragment>
        );
    }
}
EntranceContainer.propTypes = {};
EntranceContainer.defaultProps = {};
const mapStateToProps = state => {
    return state
};
const mapDispatchToProps = dispatch => ({
});
export default connect(mapStateToProps, mapDispatchToProps)(EntranceContainer);